import React, { useState, useEffect } from 'react';
import { Link as RouterLink, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import validate from 'validate.js';
import { makeStyles } from '@material-ui/styles';
import {
  Button,
  TextField,
  Link,
  Typography
} from '@material-ui/core';
import api from '../../services/api';
import { toast } from 'react-toastify';
import { cpfMask } from './../../common/mask'
import { ToastContainer } from 'react-toastify';

const schema = {
  name: {
    presence: { allowEmpty: false, message: 'O nome  obrigatrio.' },
    length: {
      minimum: 8,
      maximum: 50,
      message: 'O nome deve conter no mnimo 8 e no mximo 50 caracteres.'
    }
  },
  email: {
    presence: { allowEmpty: false, message: 'O e-mail  obrigatrio.' },
    email: true,
    length: {
      maximum: 64,
      message: 'O e-mail deve conter no mximo 64 caracteres.'
    }
  },
  password: {
    presence: { allowEmpty: false, message: 'A senha  obrigatria.' },
    length: {
      minimum: 6,
      maximum: 10,
      message: 'A senha deve conter no mnimo 6 e no mximo 10 caracteres.'
    }
  },
  /*policy: {
    presence: { allowEmpty: false, message: 'is required' },
    checked: true
  }*/
};

const useStyles = makeStyles(theme => ({
  root: {
    //backgroundColor: theme.palette.background.default,
    height: '100%'
  },
  name: {
    marginTop: theme.spacing(3),
    color: theme.palette.white
  },
  contentContainer: {},
  content: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column'
  },
  contentHeader: {
    display: 'flex',
    alignItems: 'center',
    paddingTop: theme.spacing(5),
    paddingBototm: theme.spacing(2),
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2)
  },
  logoImage: {
    paddingTop: theme.spacing(3),
    flexGrow: 1,
    display: 'flex',
    justifyContent: 'center'
  },
  contentBody: {
    flexGrow: 1,
    display: 'flex',
    justifyContent: 'center'
  },
  form: {
    paddingLeft: 100,
    paddingRight: 100,
    paddingBottom: 125,
    flexBasis: 700,
    [theme.breakpoints.down('sm')]: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(2)
    }
  },
  title: {
    marginTop: theme.spacing(3),
    fontWeight: 'bold'
  },
  textField: {
    marginTop: theme.spacing(2)
  },
  policy: {
    marginTop: theme.spacing(1),
    display: 'flex',
    alignItems: 'center'
  },
  policyCheckbox: {
    marginLeft: '-14px'
  },
  signUpButton: {
    margin: theme.spacing(2, 0)
  }
}));

const SignUp = props => {
  const { history } = props;

  const classes = useStyles();

  const [formState, setFormState] = useState({
    isValid: false,
    values: {},
    touched: {},
    errors: {}
  });

  useEffect(() => {
    const errors = validate(formState.values, schema);

    setFormState(formState => ({
      ...formState,
      isValid: errors ? false : true,
      errors: errors || {}
    }));
  }, [formState.values]);

  const handleChange = event => {
    event.persist();

    setFormState(formState => ({
      ...formState,
      values: {
        ...formState.values,
        [event.target.name]:
            event.target.name === 'cpf'
                ? formState.values.cpf = cpfMask(event.target.value)
                : event.target.value
      },
      touched: {
        ...formState.touched,
        [event.target.name]: true
      }
    }));

  };

  async function handleSignUp(e){
    e.preventDefault();
    try {
      const name = formState.values.name;
      const cpf = formState.values.cpf;
      const email = formState.values.email;
      const password = formState.values.password;

      const data = {
        name, cpf, email, password
      };

      const response = await api.post('register', data);

      if (response.status === 202) {

        if(response.data.message){
          toast.error(response.data.message);
        }

        if(response.data.errors[0].name){
          toast.error(response.data.errors[0].name[0]);
        }

        if(response.data.errors[0].cpf){
          toast.error(response.data.errors[0].cpf[0]);
        }

        if(response.data.errors[0].email){
          toast.error(response.data.errors[0].email[0]);
        }

        if(response.data.errors[0].password){
          toast.error(response.data.errors[0].password[0]);
        }
      } else {
        toast.success(response.data[0].name+', cadastrado!');
        history.push('/sign-in');
      }
    } catch (error) {

    }
  }

  const hasError = field =>
    formState.touched[field] && formState.errors[field] ? true : false;

  return (
    <div className={classes.root}>
      <div className={classes.content}>
        <div className={classes.contentBody}>
          <form
            className={classes.form}
            onSubmit={handleSignUp}>
            <div className={classes.logoImage}>
              <img
                  alt="Logo"
                  src="/images/logomarca.png"/>
            </div>
            <Typography
              className={classes.title}
              variant="h5">
              Crie uma nova conta
            </Typography>
            <TextField
              className={classes.textField}
              error={hasError('name')}
              fullWidth
              helperText={
                hasError('name') ? formState.errors.name[0] : null
              }
              label="Nome"
              name="name"
              onChange={handleChange}
              type="text"
              value={formState.values.name || ''}
              variant="outlined"
            />
            {/* <TextField
              className={classes.textField}
              error={hasError('cpf')}
              fullWidth
              helperText={
                hasError('cpf') ? formState.errors.cpf[0] : null
              }
              label="Cpf"
              name="cpf"
              onChange={handleChange}
              type="text"
              value={formState.values.cpf || ''}
              variant="outlined"
            /> */}
            <TextField
              className={classes.textField}
              error={hasError('email')}
              fullWidth
              helperText={
                hasError('email') ? formState.errors.email[0] : null
              }
              label="Email"
              name="email"
              onChange={handleChange}
              type="text"
              value={formState.values.email || ''}
              variant="outlined"
            />
            <TextField
              className={classes.textField}
              error={hasError('password')}
              fullWidth
              helperText={
                hasError('password') ? formState.errors.password[0] : null
              }
              label="Senha"
              name="password"
              onChange={handleChange}
              type="password"
              value={formState.values.password || ''}
              variant="outlined"
            />
            <Button
              className={classes.signUpButton}
              color="primary"
              disabled={!formState.isValid}
              fullWidth
              size="large"
              type="submit"
              variant="contained">
              Cadastre-se
            </Button>
            <Typography
              color="textSecondary"
              variant="body1">
              J tem uma conta?{' '}
              <Link
                component={RouterLink}
                to="/sign-in"
                variant="h6">
                Login
              </Link>
            </Typography>
          </form>
        </div>
      </div>
      <ToastContainer autoClose={8000} position="bottom-center"/>
    </div>
  );
};

SignUp.propTypes = {
  history: PropTypes.object
};

export default withRouter(SignUp);
