import React, {useEffect, useState} from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Grid,
  Button,
  TextField, IconButton, FormControlLabel, Switch, Tooltip, Typography
} from '@material-ui/core';
import api from "../../../../services/api";
import { toast } from 'react-toastify';
import validate from "validate.js";
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import {withStyles} from "@material-ui/core/styles";

const schema = {
  description: {
    presence: { allowEmpty: false,  message: 'A descrio  obrigatria.'},
    length: {
      minimum: 4,
      maximum: 300,
      message: 'A descrio deve conter no mnimo 4 e no mximo 300 caracteres.'
    }
  },
};

const useStyles = makeStyles(() => ({
  root: {}
}));

const TooltipCustomized = withStyles((theme) => ({
  tooltip: {
    backgroundColor: '#f5f5f9',
    color: 'rgba(0, 0, 0, 0.87)',
    maxWidth: 220,
    fontSize: theme.typography.pxToRem(12),
    border: '1px solid #dadde9',
  },
}))(Tooltip);

const EvaluationApplicationDetails = props => {
  const { className, history, ...rest } = props;
  const { idApplication, codigoEvaluation } = props.match.params;
  const [checkedRandom, setCheckedRandom] = React.useState(false);
  const [checkedShowResult, setCheckedShowResult] = React.useState(false);

  const classes = useStyles();

  const [formState, setFormState] = useState({
    isValid: false,
    values: {
      'show_results' : false,
      'random_questions' : false
    },
    touched: {},
    errors: {}
  });

  async function saveApplicationDetails(){
    try {
      const description = formState.values.description;
      const random_questions = checkedRandom;
      const show_results = checkedShowResult;
      const data = {
        description,
        random_questions,
        show_results
      }
      const response = await api.put('evaluation/practice/applications/'+idApplication, data);
      if (response.status === 202) {
        if(response.data.message){
          toast.error(response.data.message);
        } else if(response.data.errors[0].description){
          toast.error(response.data.errors[0].description);
        }
      } else {
        toast.success('Aplicao atualizada.');
        history.push(`/evaluation-practice/applications-evaluation/${codigoEvaluation}`);
      }

    } catch (error) {

    }
  }

  async function findAApplication(id){
    try {
      const response = await api.get('evaluation/practice/applications/show/'+id);
      if (response.status === 202) {
        if(response.data.message){
          toast.error(response.data.message);
        }
      } else {
        setCheckedRandom(response.data.random_questions == 1 ? true : false);
        setCheckedShowResult(response.data.show_results == 1 ? true : false);
        setFormState(formState => ({
          values: {
            'description': response.data.description,
          },
          touched: {
            ...formState.touched,
          }
        }));

      }
    } catch (error) {

    }
  }

  useEffect(() => {
    if(idApplication){
      findAApplication(idApplication);
    }

  }, []);

  useEffect(() => {
    const errors = validate(formState.values, schema);

    setFormState(formState => ({
      ...formState,
      isValid: (errors) ? false : true,
      errors: errors || {}
    }));
  }, [formState.values]);

  const handleChange = event => {
    setFormState({
      ...formState,
      values: {
        ...formState.values,
        [event.target.name]: event.target.value
      },
      touched: {
        ...formState.touched,
        [event.target.name]: true
      }
    });
  };

  const handleChangeRandom = event => {
    setCheckedRandom(event.target.checked);
  }

  const handleChangeShowResult = event => {
    setCheckedShowResult(event.target.checked);
  }

  const hasError = field =>
      formState.touched[field] && formState.errors[field] ? true : false;

  const handleBack = () => {
    history.goBack();
  };

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}>
      <form
        autoComplete="off">
        <div className={classes.contentHeader}>
          <IconButton onClick={handleBack}>
            <ArrowBackIcon />
          </IconButton>
        </div>
        <CardHeader
          subheader=""
          title="Aplicao de Avaliao"/>
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}>
            <Grid
              item
              md={6}
              xs={12}>
              <TextField
                fullWidth
                error={hasError('description')}
                helperText={
                  hasError('description') ? formState.errors.description[0] : null
                }
                label="Descrio"
                margin="dense"
                name="description"
                onChange={handleChange}
                value={formState.values.description || ''}
                variant="outlined"
              />
              {/* <TooltipCustomized
                  title={
                    <React.Fragment>
                      <p>
                        <Typography color="textPrimary" variant="body2">
                          {'Caso esta opo fique ativa, todos os estudantes que forem realizar ' +
                          ' esta aplicao recebero' +
                          ' uma avaliao com as mesmas questes, mas cada estudante possuir uma ' +
                          ' avaliao com questes em ordem diferente das demais.'}
                        </Typography>
                      </p>
                    </React.Fragment>
                  }>
                  <FormControlLabel
                      control={
                        <Switch
                            checked={checkedRandom}
                            onChange={handleChangeRandom}
                            name="random_questions"
                            color="primary"
                        />
                      }
                      label="Questes aleatrias?"
                  />
              </TooltipCustomized> */}
              {/* <TooltipCustomized
                  title={
                    <React.Fragment>
                      <p>
                        <Typography color="textPrimary" variant="body2">
                          {'Caso esta opo fique ativa, todos os estudantes tero acesso' +
                          ' ao resultado da sua avaliao. No resultado  apenas apresentado' +
                          ' se o estudante acertou ou errou as questes. A questo completa no' +
                          '  apresentada ao estudante.'}
                        </Typography>
                      </p>
                    </React.Fragment>
                  }>
                <FormControlLabel
                    control={
                      <Switch
                          checked={checkedShowResult}
                          onChange={handleChangeShowResult}
                          name="show_results"
                          color="primary"
                      />
                    }
                    label="Liberar o resultado?"
                />
              </TooltipCustomized> */}
            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            color="primary"
            variant="outlined"
            onClick={saveApplicationDetails}
            disabled={!formState.isValid}>
            Salvar
          </Button>
        </CardActions>
      </form>
    </Card>
  );
};

EvaluationApplicationDetails.propTypes = {
  className: PropTypes.string,
};

export default EvaluationApplicationDetails;
